/* AdminDashboard.css */

.admin-dashboard {
  display: flex;
}

.sidebar {
  width: 300px;
  background-color: #050505;
  color: #fff;
  padding: 20px;
  height: 100vh;
  position: relative;
}

nav {
ul {
  list-style: none;
  padding: 0;
  margin: 0;
  overflow-y: auto; /* Add overflow-y property for vertical scrollbar */
  max-height: 27vw;

}
.nav-link {
  display: flex;
  padding: 18px 37px;
  /* border-bottom: 1px solid #555; */
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  text-decoration: none;
  color: #767676;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  text-transform: capitalize;
  margin-bottom: 12px;
}
svg{
  margin-right: 5px;
  fill: #767676 !important;
}
  .nav-link:hover {
    color: #fff;
    border-radius: 6px;
    background: #222;
  }
  .nav-link:hover svg{
    margin-right: 5px;
    fill: #ffffff !important;
  }
  .activeWindow svg{
    fill: #fff !important;
    }
    .activeWindow{
    background: #222;
    border-radius: 6px;
    color: #fff;
  }
}
.logo-nav{
  display: flex;
  margin: 0 auto;
  margin-top: 30%;
  justify-content: center;
  margin-bottom: 60px;
  height: 100px;
  width: 100px;
  position: relative;
}
.main-content {
  flex: 1;
  height: 100vh;
  overflow-y: scroll;
  position: relative;
}

.main-content::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
  margin-right: 5px;
}

.main-content::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #333;
  margin-right: 5px;
}

.cards {
  display: flex;
  gap: 20px;
}

.card {
  /* Add card styling */
}

.profile-section {
  position: fixed;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  margin: 92px 72px 0px 0px;
  z-index: 1;
  /* width: 300px; */
  justify-content: flex-end;
  padding: 7px;
  background: #fff;
  border-radius: 12px;
}

.profile-image {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  margin-right: 12px;
}

.admin-name {
  color: #000;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px; /* 100% */
  text-transform: capitalize;
}
.logout-link {
  cursor: pointer;
  margin-top: auto; /* Push the logout link to the bottom */
  padding: 10px;
  border-top: 1px solid #555;
  display: flex;
  padding: 18px 37px;
  /* border-bottom: 1px solid #555; */
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  text-decoration: none;
  color: #767676;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  text-transform: capitalize;
  position: absolute;
  bottom: 0;
  width: 85%;
  background-color: #000;
}
.logout-link:hover{
  color: #ffffff;
}
.logout-link svg{
  margin-right: 12px;
}
.logout-link:hover svg{
  fill: #fff;
}
/* Webkit Scrollbar Styles */
nav ul::-webkit-scrollbar {
  width: 9px;
  margin-left:10px ;
  border-radius: 15px;
}

nav ul::-webkit-scrollbar-thumb {
  background-color: #FCE6ED;
}

nav ul::-webkit-scrollbar-track {
  background-color: #333;
}
