/* styles.css */

.image-upload {
  display: flex;
  height: 212px;
  padding: 73px 160px 72px 161px;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  border: 1px solid #E7E7E7;
  background: #FFF;
  display: flex;
  position: relative;
}
.custom-file-upload-btn{
  display: flex;
  flex-direction: column;
  align-content: center;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 0;
}
.custom-file-upload-btn svg{
  margin-bottom: 12px;
}
.admin-dashboard .main-content .form-container .frm-row .one-row .custom-file-upload-btn .dragUpload{
  color: #D7D7D7;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 12px; /* 85.714% */
  text-transform: capitalize;
  margin-bottom: 12px;
  display: block;
}
.admin-dashboard .main-content .form-container .frm-row .one-row .custom-file-upload-btn .browse{
  color: #FF749E;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 12px; /* 100% */
  text-transform: capitalize;
  display: block;
}

.nav-link > svg, .nav-link > img {
  margin-right: 15px;
}

nav > ul > li > .nav-link, nav > ul > li > .nav-link:focus {
  color: #767676;
  font-size: 14px;
  line-height: 14px;
  font-weight: 600;
  margin-bottom: 15px;
}

nav > ul > li > .nav-link.active, nav > ul > li > .nav-link:hover {
  background-color: #222222;
  border-radius: 6px;
  color: white;
  font-size: 14px;
  line-height: 14px;
  font-weight: 600;
}

.user-leaderboard-user-list {
  gap: 12px;
}

.user-leaderboard-user-list > .col {
  background-color: white;
  border-radius: 6px;
  padding: 16px;
  width: calc(25% - 12px);
  cursor: pointer;
}

.user-leaderboard-user-list > .col > div {
  display: flex;
  align-items: center;
}

.user-leaderboard-user-list > .col > div:first-child {
  padding-bottom: 12px;
  border-bottom: 1px solid #E7E7E7;
}

.user-leaderboard-user-list > .col > div:last-child {
  padding-top: 12px;
}

.user-leaderboard-user-list > .col > div > img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  margin-right: 22px;
}

.user-leaderboard-user-list > .col > div > .user-detail-container {
  width: 100%;
  text-align: right;
  overflow-wrap: anywhere;
}

.user-leaderboard-user-list > .col > div > .user-detail-container > .user-name {
  display: block;
  color: #4E4E4E;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-transform: capitalize;
  padding-bottom: 4px;
}

.user-leaderboard-user-list > .col > div > .user-detail-container > .user-email {
  display: block;
  color: #9B9B9B;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
  text-transform: lowercase;
}

@media (max-width: 1800px) {
    td button {
        margin-left: 35%;
    }
}

@media (max-width: 1650px) {
  .user-leaderboard-user-list > .col {
    width: calc((100% / 3) - 12px);
  }
}

@media (max-width: 1550px) {
    td button {
        margin-left: 25%;
    }
}

@media (max-width: 1400px) {
    .user-leaderboard-user-list > .col {
        width: calc(50% - 12px);
    }

    td button {
        margin-left: 0;
    }
}

@media (max-width: 1050px) {
    .user-leaderboard-user-list > .col {
        width: calc(100% - 12px);
    }
}


